/**
 * The following styles get applied inside the editor only.
 *
 * Replace them with your own styles or remove the file completely.
 */

.wp-block-dynamic-tables-dynamic-tables {
	border: 1px dotted #f00;
}

.border-box-workaround {
	width: 100%;
}

.border-box-workaround .components-base-control {
	margin-bottom: 0;
}

.header {
	justify-self: center;
	align-self: end;
	font-weight: bold;
}

.body {
	justify-self: start;
}

.inspector-controls-menu__header-alignment--top {
	display: flex;
	width: 100%;
	align-items: flex-start;
}

.inspector-controls-menu__header-alignment--middle {
	display: flex;
	width: 100%;
	align-items: center;
}

.inspector-controls-nemu__label--left-margin {
	margin-left: 12px;
}

.grid-control__border-cells {
	font-size: small;
	font-weight: bold;
	text-align: center;
	vertical-align: middle;
	margin: 0px;
	padding: 0px;
	color: dimgray;
	background-color: ghostwhite;
	border-color: lightgray;
	border-style: solid;
	border-width: 1px;

	// mercury - For Selected Column/Row
	&:hover {
		cursor: pointer;
	}
}

.grid-control__inspector-controls--read-only {
	display: flex;
	align-items: center;
	flex-direction: row;
	gap: 8px;
}

.grid-control__inspector-controls--read-only-label {
	font-size: 11px;
	font-weight: 500;
	text-overflow: ellipsis;
	text-transform: uppercase;

}

.grid-control__inspector-controls--border-box-control {
	height: 15px;
}

.column-menu {
	visibility: hidden;
}

.wp-block-table {
	// Remove default <figure> style.
	margin: 0;

	.wp-block[data-align="left"]>&,
	.wp-block[data-align="right"]>&,
	.wp-block[data-align="center"]>& {
		// Stop table block from collapsing when tables are floated.
		height: auto;

		table {
			// Ensure the table element is not full-width when aligned.
			width: auto;
		}
	}

	.wp-block[data-align="center"]>& {
		text-align: initial;

		table {
			margin: 0 auto;
		}
	}

}

.blocks-table__placeholder-form.blocks-table__placeholder-form {
